.risk-assets-card {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  transition: all 0.2s ease;
}
.risk-assets-card:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  border-color: #d1d5db;
}
.risk-assets-card .card-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.5rem;
}
.risk-assets-card .card-header .card-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0;
}
.risk-assets-card .card-header .asset-count {
  padding: 0.25rem 0.75rem;
  background: #f3f4f6;
  color: #6b7280;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
}
.risk-assets-card .assets-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.risk-assets-card .assets-list .asset-item {
  border: 1px solid #e5e7eb;
  border-radius: 0.75rem;
  padding: 1rem;
  transition: all 0.2s ease;
}
.risk-assets-card .assets-list .asset-item:hover {
  border-color: #d1d5db;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.1);
}
.risk-assets-card .assets-list .asset-item.risk-high {
  border-left: 4px solid #dc2626;
  background: #fef2f2;
}
.risk-assets-card .assets-list .asset-item.risk-medium {
  border-left: 4px solid #f59e0b;
  background: #fffbeb;
}
.risk-assets-card .assets-list .asset-item.risk-low {
  border-left: 4px solid #22c55e;
  background: #f0fdf4;
}
.risk-assets-card .assets-list .asset-item .asset-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}
.risk-assets-card .assets-list .asset-item .asset-header .asset-name {
  font-size: 1rem;
  font-weight: 600;
  color: #1f2937;
}
.risk-assets-card .assets-list .asset-item .asset-header .risk-badge {
  padding: 0.25rem 0.5rem;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.025em;
}
.risk-assets-card .assets-list .asset-item .asset-header .risk-badge.risk-high {
  background: #fecaca;
  color: #dc2626;
}
.risk-assets-card .assets-list .asset-item .asset-header .risk-badge.risk-medium {
  background: #fde68a;
  color: #d97706;
}
.risk-assets-card .assets-list .asset-item .asset-header .risk-badge.risk-low {
  background: #bbf7d0;
  color: #166534;
}
.risk-assets-card .assets-list .asset-item .asset-reason {
  font-size: 0.875rem;
  color: #6b7280;
  line-height: 1.5;
  margin-bottom: 0.75rem;
}
.risk-assets-card .assets-list .asset-item .asset-details {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 0.75rem;
}
.risk-assets-card .assets-list .asset-item .asset-details .confidence-chip {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  padding: 0.25rem 0.5rem;
  background: #e0e7ff;
  color: #4338ca;
  border-radius: 0.375rem;
  font-size: 0.75rem;
  font-weight: 500;
}
.risk-assets-card .assets-list .asset-item .asset-details .confidence-chip svg {
  width: 0.75rem;
  height: 0.75rem;
}
.risk-assets-card .assets-list .asset-item .asset-details .cost-info {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  font-size: 0.75rem;
}
.risk-assets-card .assets-list .asset-item .asset-details .cost-info .cost-label {
  color: #6b7280;
}
.risk-assets-card .assets-list .asset-item .asset-details .cost-info .cost-value {
  font-weight: 600;
  color: #1f2937;
}
.risk-assets-card .assets-list .asset-item .asset-actions {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 0.75rem;
}
.risk-assets-card .assets-list .asset-item .asset-actions .action-btn {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  padding: 0.5rem 0.75rem;
  border-radius: 0.5rem;
  font-size: 0.75rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  border: 1px solid transparent;
}
.risk-assets-card .assets-list .asset-item .asset-actions .action-btn.primary {
  background: #3b82f6;
  color: white;
  border-color: #3b82f6;
}
.risk-assets-card .assets-list .asset-item .asset-actions .action-btn.primary:hover {
  background: #2563eb;
  transform: translateY(-1px);
}
.risk-assets-card .assets-list .asset-item .asset-actions .action-btn.secondary {
  background: white;
  color: #374151;
  border-color: #d1d5db;
}
.risk-assets-card .assets-list .asset-item .asset-actions .action-btn.secondary:hover {
  background: #f9fafb;
  border-color: #9ca3af;
  transform: translateY(-1px);
}
.risk-assets-card .assets-list .asset-item .asset-actions .action-btn svg {
  width: 0.875rem;
  height: 0.875rem;
}
.risk-assets-card .assets-list .asset-item .recommended-action {
  font-size: 0.875rem;
  color: #374151;
  line-height: 1.5;
  padding: 0.75rem;
  background: #f8fafc;
  border-radius: 0.5rem;
  border: 1px solid #e2e8f0;
}
.risk-assets-card .empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem 1rem;
  text-align: center;
}
.risk-assets-card .empty-state .empty-icon {
  color: #d1d5db;
  margin-bottom: 1rem;
}
.risk-assets-card .empty-state .empty-title {
  font-size: 1.125rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 0.5rem 0;
}
.risk-assets-card .empty-state .empty-description {
  font-size: 0.875rem;
  color: #6b7280;
  margin: 0;
  line-height: 1.5;
}

@media (max-width: 768px) {
  .risk-assets-card {
    padding: 1rem;
  }
  .risk-assets-card .card-header {
    flex-direction: column;
    align-items: stretch;
    gap: 0.5rem;
    text-align: center;
  }
  .risk-assets-card .assets-list .asset-item .asset-header {
    flex-direction: column;
    align-items: stretch;
    gap: 0.5rem;
    text-align: center;
  }
  .risk-assets-card .assets-list .asset-item .asset-details {
    flex-direction: column;
    align-items: stretch;
    gap: 0.5rem;
  }
  .risk-assets-card .assets-list .asset-item .asset-actions {
    flex-direction: column;
  }
  .risk-assets-card .assets-list .asset-item .asset-actions .action-btn {
    justify-content: center;
  }
}
